import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Listoo from '../views/Listoo.vue'
import EditingDesk from '../views/EditingDesk.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Listoo',
    name: 'Listoo',
    component: Listoo
  },
   {
    path: '/EditingDesk',
    name: 'EditingDesk',
    component: EditingDesk
  },


]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
